.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__logo{
        width: 250px;
        img{
            width: 100%;
        }
    }
    &__offical{
        color: #ffffff;
        font-size: 12px;
        font-weight: 400;
        text-align: center;
        span{
            display: block;
            font-size: 18px;
        }
    }
    &__phone{
        display: block;
        color: #FFF;
        font-size: 18px;
        font-weight: 700;
        &:hover{
            color: #FFF;
            text-decoration: none;
        }
    }
    &__contact{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

}