.button{
    width: 150px;
    height: 30px;
    border-radius: 5px;
    background-color: $main-color;
    border: none;
    
    color: #FFF;
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    &:hover{
        background-color: #980a0a
    }

    &_main{
        margin-top: 68px;
        width: 300px;
        height: 50px;
        font-size: 16px;
    
    }
    &_submit{
        width: 100%;
        height: 55px;
        background-color: $main-color;
        color: #FFF;
        text-align: center;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        text-transform: uppercase;
            }
    
}